import { useMemo, useState, useCallback, useEffect } from 'react';
import type { MaiMaiMood, UseMaiMaiMoodOptions } from './types';

/**
 * MaiMai ÂøÉÊÉÖÁãÄÊÖãÊ©ü Hook
 * @description Ê†πÊìöÂ§öÁ®ÆËº∏ÂÖ•Ë®àÁÆóÁï∂ÂâçÊáâÈ°ØÁ§∫ÁöÑÂøÉÊÉÖÁãÄÊÖã
 * 
 * ÂÑ™ÂÖàÁ¥öÈ†ÜÂ∫èÔºàÈ´òÂà∞‰ΩéÔºâÔºö
 * 1. Â§ñÈÉ®Âº∑Âà∂ÊåáÂÆö (externalMood)
 * 2. ÈªûÊìäÊÖ∂Á•ù (isCelebrating)
 * 3. ÊàêÂäüÁãÄÊÖã (isSuccess)
 * 4. ÈåØË™§ÁãÄÊÖã (hasError)
 * 5. ËºâÂÖ•‰∏≠ (isLoading)
 * 6. Ëº∏ÂÖ•ÂØÜÁ¢º (isTypingPassword)
 * 7. Ëº∏ÂÖ• email (isTypingEmail)
 * 8. hover ÁãÄÊÖã (isHovered)
 * 9. È†êË®≠ idle
 */
export function useMaiMaiMood(options: UseMaiMaiMoodOptions = {}): {
  mood: MaiMaiMood;
  clickCount: number;
  handleClick: () => void;
  resetCelebration: () => void;
} {
  const {
    externalMood,
    isSuccess = false,
    hasError = false,
    isLoading = false,
    isTypingPassword = false,
    isTypingEmail = false,
    isHovered = false,
    isCelebrating: externalCelebrating,
  } = options;

  const [clickCount, setClickCount] = useState(0);
  const [internalCelebrating, setInternalCelebrating] = useState(false);

  // ÈªûÊìä 5 Ê¨°Ëß∏ÁôºÊÖ∂Á•ù
  const handleClick = useCallback(() => {
    setClickCount(prev => {
      const newCount = prev + 1;
      if (newCount >= 5) {
        setInternalCelebrating(true);
        // 2 ÁßíÂæåÈáçÁΩÆ
        setTimeout(() => {
          setInternalCelebrating(false);
          setClickCount(0);
        }, 2000);
      }
      return newCount;
    });
  }, []);

  const resetCelebration = useCallback(() => {
    setInternalCelebrating(false);
    setClickCount(0);
  }, []);

  // ‰ΩøÁî® useMemo Ë®àÁÆó moodÔºåÈÅøÂÖçÈñÉÁàç
  const mood = useMemo<MaiMaiMood>(() => {
    // ÂÑ™ÂÖàÁ¥ö 1: Â§ñÈÉ®Âº∑Âà∂ÊåáÂÆö
    if (externalMood) return externalMood;
    
    // ÂÑ™ÂÖàÁ¥ö 2: ÊÖ∂Á•ùÁãÄÊÖãÔºàÈªûÊìäÊàñÂ§ñÈÉ®Ëß∏ÁôºÔºâ
    if (internalCelebrating || externalCelebrating) return 'celebrate';
    
    // ÂÑ™ÂÖàÁ¥ö 3: ÊàêÂäü
    if (isSuccess) return 'celebrate';
    
    // ÂÑ™ÂÖàÁ¥ö 4: ÈåØË™§
    if (hasError) return 'shy';
    
    // ÂÑ™ÂÖàÁ¥ö 5: ËºâÂÖ•‰∏≠
    if (isLoading) return 'thinking';
    
    // ÂÑ™ÂÖàÁ¥ö 6: Ëº∏ÂÖ•ÂØÜÁ¢º
    if (isTypingPassword) return 'peek';
    
    // ÂÑ™ÂÖàÁ¥ö 7: Ëº∏ÂÖ• email
    if (isTypingEmail) return 'happy';
    
    // ÂÑ™ÂÖàÁ¥ö 8: hover
    if (isHovered) return 'wave';
    
    // È†êË®≠
    return 'idle';
  }, [
    externalMood,
    internalCelebrating,
    externalCelebrating,
    isSuccess,
    hasError,
    isLoading,
    isTypingPassword,
    isTypingEmail,
    isHovered,
  ]);

  return {
    mood,
    clickCount,
    handleClick,
    resetCelebration,
  };
}

/**
 * Áõ£ËÅΩÂÖ®Âüü mascot:celebrate ‰∫ã‰ª∂ÁöÑ Hook
 */
export function useMascotCelebrateEvent(onCelebrate: () => void) {
  useEffect(() => {
    const handler = () => onCelebrate();
    window.addEventListener('mascot:celebrate', handler);
    return () => window.removeEventListener('mascot:celebrate', handler);
  }, [onCelebrate]);
}
